import Vega from './libs/vega.js';
import goto from './directives/goto';
import clickout from './directives/clickout';
import { g7s } from './libs/util';

export default {
    install(Vue, config) {

        let configList = [];
        if (config instanceof Array) {
            configList = [].concat(config);
        } else if (typeof config === 'object') {
            configList = [config];
        }

        configList.forEach((item) => {
            const { apis, vega, product, name = '$vega' } = item;
            // vage, veganame$vage
            const vegaInstance = new Vega({ apis, vega, Vue });

            Vue.prototype[name] = vegaInstance.getApi();

            // , 
            // g7svue
            if (!Vue.prototype.$g7s) {

                Vue.use(goto, {
                    product,
                });

                Vue.use(clickout);

                Vue.prototype.$g7s = g7s;
            }
        })
    },
};
