import qs from 'qs';
import Cookies from 'js-cookie';
import crypto from 'crypto';
import G7s from './g7s';

/**
 *
 * @param {String} reqType 
 * @param {String} path 
 * @param {string} model 
 * @param {string} version vega
 */
export const makeApiSign = (config) => {
    const { reqType, path, model = 'truck', version = 'v1', accessId, secretKey } = config;
    const timestamp = Date.now();
    const stringToSign = `${reqType.toUpperCase()}\n${timestamp}\n/${version}/${model}${path}`;
    const sign = crypto.createHmac('sha1', secretKey).update(stringToSign).digest().toString('base64');
    return {
        accessid: accessId,
        sign,
        token: Cookies.get('_TOKEN') || getEnvInfo().token, // token
        g7timestamp: timestamp,
    };
}

/**
 *
 * @param {*} reqType 
 * @param {*} path vega
 * @param {*} model 
 * @param {*} version vega 
 * @return 
 */
export const makeApiUrl = (reqType, path, model = 'truck', version = 'v1') => {
    const signParam = makeApiSign(reqType, path, model, version);
    return `?accessid=${signParam.accessid}&sign=${encodeURIComponent(signParam.sign)}&token=${signParam.token}&g7timestamp=${signParam.g7timestamp}`;
}


/**
 * 
 * @return Object
 */
export const parseUrl = () => {
    const result = {
        env: 'local',
        token: '',
    }
    if (!location || !location.search) return result;
    Object.assign(result, qs.parse(location.search, { ignoreQueryPrefix: true }));
    Object.assign(result, {
        env: result.env ? result.env : 'local',
    });
    return result;
}

/**
 * 
 * @return Object
 *      1local 
 *      2env
 *      3tokentoken
 */
export const getEnvInfo = () => {
    const envInfo = {
        local: !!(location.hostname === 'localhost' || location.hostname.slice(0, 6) === '172.22' || location.hostname === '127.0.0.1'),
        env: location.hostname.split('.')[0] || 'online',
        token: Cookies.get('_TOKEN'),
    };
    // token
    if (envInfo.local) {
        Object.assign(envInfo, parseUrl());
    }
    return envInfo;
}

/**
 * g7s
 */
export const g7s = new G7s();

/**
 * @param {*} routes 
 */
export const goto = (routes) => {
    const env = getEnvInfo();
    if (env.local) {
        window.routerInstance.push(routes);
    } else {
        const resolvePath = window.routerInstance.resolve(routes);
        const url = resolvePath.resolved && resolvePath.resolved.fullPath ? resolvePath.resolved.fullPath : '';
        if (url) {
            g7s.goto({
                url: `${window.config.product}/v3${url}`,
                system: false, // getSubsystemByUrl 
                blank: routes.blank || false,
                hash: true,
            });
        } else {
            console.error('');
        }
    }
};
